import React from 'react';
import AniLink from 'gatsby-plugin-transition-link/AniLink';

// Const & Utils
import { useWindowSize } from '../../utils/useWindowSize';
import { mediaSize } from '../../constants/theme';
import { colors } from '../../constants/theme';

// Components
import { Image } from '../../components';

export const Link = ({ children, ...props }) => {
  const logoPath = Image({ src: 'aristo-icon-light.png', linkOnly: true });
  const condensedNav = useWindowSize().width <= mediaSize.condensedNav;
  return (
    <AniLink
      cover
      direction="top"
      bg={`url(${logoPath.src}) center no-repeat ${
        condensedNav ? colors.darkBg : colors.brand
      }`}
      {...props}>
      {children}
    </AniLink>
  );
};
